import React, { useState } from "react";

export default function TextForm(props) {
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert("Converted to uppercase", "success");    
  };
  const handleLowerClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
    props.showAlert("Converted to lowercase", "success");
  };
  const handleClearClick = () => {
    let newText = "";
    setText(newText);
    props.showAlert("Textarea cleared", "success");
  };
  const capitalizeFirstLetter = () => {
    let newText = text.split(" ").map((currentValue) => {
      let newText =
        currentValue.charAt(0).toUpperCase() + currentValue.slice(1);
      return newText;
    });
    setText(newText.join(" "));
  };
  const handleExtraSpaces = () => {
    let newText = text.split(/[ ]+/);    
    setText(newText.join(" "));
    props.showAlert("Extra Spaces removed", "success");
  };

  const handleOnChange = (event) => {
    setText(event.target.value);
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(text);
    props.showAlert("Text Copied", "success");
  };
  const handlePresent = () => {
    let newtext = prompt();
    alert(text.includes(newtext));
  };
  const handleReverse = () => {
    let newtext = text.split("");
    let reverse = newtext.reverse();
    let joined = reverse.join("");
    setText(joined);
    props.showAlert("Text Reversed", "success");
  };

  const [text, setText] = useState("");
  // text = "some value"; //wrong way to change values
  //setText("new text"); //Correct way
  return (
    <>
      <div className="container my-3" style={{color: props.mode === "light" ? "black" : "white"}}>
      <h1>{props.heading}</h1>
      <div className="mb-3">
          <textarea className="form-control" value={text} id="myBox" rows="8" style={{backgroundColor: props.mode === "light" ? "white" : "grey", color: props.mode === "light" ? "black" : "white",}} onChange={handleOnChange}></textarea>
        </div>        
      </div>
      <div className="container">
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleLowerClick}>Convert to LowerCase</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleUpClick}>Convert to UpperCase</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleClearClick}>Clear Text Area</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={capitalizeFirstLetter}>Capitalize First Letter</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleCopy}>Copy Text</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleExtraSpaces}>Handle Extra Spaces</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handlePresent}>Check Word Present</button>
          <button disabled={text.split(/\s+/).filter((element) => {return element.length!==0} ).length===0} className="btn btn-primary mx-2 my-2" onClick={handleReverse}>Reverse</button>
      </div>
      <div className="container" style={{ color: props.mode === "light" ? "black" : "white" }}>
        <h2>Your Text SUmmary</h2>
        <p>
          {text.split(/\s+/).filter((element) => {return element.length!==0} ).length} words, {text.length} characters
        </p>
        <p>{text.split(" ").filter((element) => {return element.length!==0} ).length * 0.008} minutes read</p>
        <h2>Preview</h2>
        <p>{text.length > 0 ? text : "Nothing to preview"}</p>
      </div>
    </>
  );
}
